@import "colors";

$border: 1px solid $border-color;

html {
  height: 100%;
}

::-webkit-scrollbar {
  -webkit-appearance: none;
  width: 8px;
  height: 8px;
}
::-webkit-scrollbar-thumb {
  border-radius: 4px;
  background-color: #e2e2e2;
  box-shadow: 0 0 1px rgba(255, 255, 255, 0.5);
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0px;
  background-color: $themeBg;
}
#root {
  display: flex;
  flex: 1;
  flex-direction: column;
  position: relative;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  min-height: 100vh;
}
.fixed-body {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.main-app {
  display: flex;
  flex-direction: column;
  flex: 1;
}
.center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.Header {
  padding: 4px 10px;
  background-color: $theme;
  display: flex;
  min-height: 64px;
  color: $white;
  position: sticky;

  z-index: 10000;
  width: 100%;
  box-shadow: 0px 2px 10px grey;
  top: 0px;
  .search {
    margin: 8px 0 4px;
    width: 50%;
    min-width: 250px;
    border-bottom: solid 2px $white;
    display: flex;
    flex-direction: row;
    &_icon {
      margin: 5px 15px 5px 10px;
    }
    &_input {
      color: $white;
      width: 100%;
      border-width: 0px;
      background-color: transparent;
      outline: none;
      align-self: flex-end;
      margin-bottom: 6px;
    }
    ::placeholder {
      /* Chrome, Firefox, Opera, Safari 10.1+ */
      color: $white;
      opacity: 0.5; /* Firefox */
      font-weight: bold;
    }

    :-ms-input-placeholder {
      /* Internet Explorer 10-11 */
      color: $white;
      opacity: 0.5;
    }

    ::-ms-input-placeholder {
      /* Microsoft Edge */
      color: $white;
      opacity: 0.5;
    }
  }
  .search-wrapper {
    width: 100%;
  }
  &_icon {
    margin: 5px 10px;
    min-width: 14px;
  }
  .dropdown-link {
    padding: 0px 10px;
  }
}

.marginPadding {
  margin: 0;
  padding: 0;
}

.loading {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba($color: $themeBg, $alpha: 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000000;
}

.Home {
  display: flex;
  flex-direction: column;
  flex: 1;
  z-index: 100;
  padding: 30px 5% 55px;
  &_noResult {
    flex: 1;
    background-color: rgba(255, 255, 255, 0.4);
    align-items: center;
    justify-content: center;
    display: flex;
    flex-direction: column;
    .title {
      margin-top: 15px;
    }
  }
  &_body {
    background-color: $themeBg;
    flex: 1;
    align-items: unset;
    flex-wrap: wrap;
    &_title {
      margin: 12px 0px;
    }
    &_container {
      display: flex;
      flex-direction: column;
      background-color: rgba(255, 255, 255, 0.8);
      justify-content: center;
    }
  }
  &_table {
    min-height: 550px;
    width: 100%;
  }
}

.dropdown-menu {
  .dropdown-item:active {
    background-color: $theme;
    color: $black;
    // background-color: unset;
    outline: none;
  }
  .active {
    color: $black;
    background-color: $theme;
    // background-color: unset;
  }
}

.page-item.active .page-link {
  background-color: $theme;
  border-color: $theme;
}
